export default {
    addCar(context, payload) {
        return new Promise((resolve) => {
            let product = context.state.goodsList.find(function (item) {
                return item.id === payload.id
            })
            if (product) {
                context.commit("addGoodsCount", product)
                resolve("商品数量+1")
            } else {
                context.commit("addGoodsType", payload)
                resolve("添加新商品")
            }
        })
    }
}
